import react, {Component} from 'react';
import {Container, Title} from'bloomer'

const logos ={
    backgroundColor: '#009688',
    height: '40px',
    padding: '10px',
    color:'white'
}

export default class Login extends Component{

    render(){
        return(
            <div style={logos}>
            <Container>
                <form></form>
                <Title isSize={5} style={{color:'white'}}>Please Login</Title>
                <span>test</span>
                </Container></div>
        )
    }

}


